/*
Note: any .css or .scss files included in the 'styles' directory
will be correctly compiled during `gulp serve` and `gulp`
 */

html, body {
  font-family: 'Roboto', 'Helvetica', sans-serif;
  margin: 0;
  padding: 0;
}
.mdl-layout__content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.mdl-layout__content .page-content {
  display: flex;
  flex-direction: column;
  height: 80vw;
  width: 80vw;
  max-height: 400px;
  max-width: 400px;
}

.control-top, .control-bottom, .control-middle {
  display: flex;
  justify-content: center;
  align-items: center;
}
.control-top, .control-bottom {
  height: 20%;
}
.control-middle {
  height: 60%;
  display: flex;
  flex-direction: row;
}

.control-left, .control-right, .control-ok {
  display: flex;
}
.control-left, .control-right {
  width: 20%;
  justify-content: center;
}
.control-ok {
  width: 60%;
  height: 100%;
}

.control-ok button {
  height: 60%;
  width: 60%;
}

.button-arrow.mdl-button--icon {
  font-size: 48px;
  height: 48px;
  width: 48px;
}

.button-arrow.mdl-button--icon .material-icons {
  -webkit-transform: translate(-24px,-24px);
  transform: translate(-24px,-24px);
  line-height: 48px;
  width: 48px;
  font-size: 48px;
}

/* SIDE NAV */
@media (max-width: 480px) {
  .mdl-layout__drawer {
    width: 100%;
    -webkit-transform: translateX(-100%);
    transform: translateX(-100%);
  }
}
